import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import errorPizza from './../assets/img/pizza-error.jpg';

const FullPizza = () => {
    const [pizza, setPizza] = useState<{
        imageUrl: string;
        title: string;
        price: number;
    }>();
    const [error, setError] = useState<any | null>(null);
    const { id } = useParams();
    const navigate = useNavigate();

    useEffect(() => {
        async function fetchPizza() {
            try {
                const { data } = await axios.get(`https://pizzas-server.glitch.me/pizzas/${id}`);
                setPizza(data);
            } catch (error) {
                setError(error);
            }
        };

        fetchPizza();
    }, []);

    if (error) {
        setTimeout(() => {
            navigate('/');
        }, 1500);

        return (
            <div className="container" style={{ textAlign: 'center' }}>
                <img style={{ width: 100 }} src={errorPizza} alt="error-pizza" />
                <h2>Произошла ошибка попробуйте позднее...</h2>
            </div>
        )
    }

    if (!pizza) {
        return (
            <div className="container">
                <h2>Загрузка...</h2>
            </div>
        )
    }

    return (
        <div className="container">
            <img src={pizza.imageUrl} alt="photo-pizza" />
            <h2>{pizza.title}</h2>
            <p>{pizza.price}</p>
            <Link to="/">
            <button className="button button--outline button--add">
                <span>Назад</span>
            </button>
            </Link>
        </div>
    );
}

export default FullPizza;